import request from '@/utils/request'

// 
export function listBuildCost(query) {
  return request({
    url: '/cost/build',
    method: 'get',
    params: query
  })
}

// id
export function getBuildCost (query) {
  return request({
    url: '/cost/build',
    method: 'get',
    params: query
  })
}

// 
export function addBuildCost (data) {
  return request({
    url: '/cost/build',
    method: 'post',
    data: data
  })
}

// 
export function updateBuildCost (data) {
  return request({
    url: '/cost/build',
    method: 'post',
    data: data
  })
}

// 
export function delBuildCost (data) {
  return request({
    url: '/cost/build/del',
    method: 'post',
    data: data
  })
}



