<template>
  <div class="app-container">
    <el-row :gutter="20">
      <el-col :xs="24">
        <el-form :model="queryParams" ref="queryForm" :inline="true" v-show="showSearch" label-width="68px">
          <el-form-item label="" prop="name">
            <el-input
              v-model="queryParams.name"
              placeholder=""
              clearable
              size="small"
              style="width: 240px"
              @keyup.enter.native="handleQuery"
            />
          </el-form-item>
          <el-form-item label="" prop="purchaser">
            <el-input
              v-model="queryParams.purchaser"
              placeholder=""
              clearable
              size="small"
              style="width: 240px"
              @keyup.enter.native="handleQuery"
            />
          </el-form-item>
          <el-form-item label="">
            <el-date-picker
              v-model="dateRange"
              size="small"
              style="width: 240px"
              value-format="yyyy-MM-dd"
              type="daterange"
              range-separator="-"
              start-placeholder=""
              end-placeholder=""
            ></el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
            <el-button icon="el-icon-refresh" size="mini" @click="resetQuery"></el-button>
          </el-form-item>
        </el-form>

        <el-row :gutter="10" class="mb8">
          <el-col :span="1.5">
            <el-button
              type="primary"
              plain
              icon="el-icon-plus"
              size="mini"
              @click="handleAdd"
            >
            </el-button>
          </el-col>
          <el-col :span="1.5">
            <el-button
              type="success"
              plain
              icon="el-icon-edit"
              size="mini"
              :disabled="single"
              @click="handleUpdate"
            >
            </el-button>
          </el-col>
          <el-col :span="1.5">
            <el-button
              type="danger"
              plain
              icon="el-icon-delete"
              size="mini"
              :disabled="multiple"
              @click="handleDelete"
            >
            </el-button>
          </el-col>
        </el-row>

        <el-table v-loading="loading" :data="buildCostList" @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="50" align="center"/>
          <el-table-column label="" align="center" key="xh" prop="xh"/>
          <el-table-column label="" align="center" key="name" prop="name" :show-overflow-tooltip="true"/>
          <el-table-column label="()" align="center" key="price" prop="price" :show-overflow-tooltip="true"/>
          <el-table-column label="" align="center" key="count" prop="count" :show-overflow-tooltip="true"/>
          <el-table-column label="" align="center" key="purchaser" prop="purchaser"/>
          <el-table-column label="" align="center" prop="buyTime">
            <!-- <template slot-scope="scope">
               <span>{{ parseTime(scope.row.buyTime) }}</span>
             </template>-->
          </el-table-column>
          <el-table-column
            label=""
            align="center"
            class-name="small-padding fixed-width"
          >
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="text"
                icon="el-icon-edit"
                @click="handleUpdate(scope.row)"
              >
              </el-button>
              <el-button
                v-if="scope.row.userId !== 1"
                size="mini"
                type="text"
                icon="el-icon-delete"
                @click="handleDelete(scope.row)"
              >
              </el-button>
            </template>
          </el-table-column>
        </el-table>

        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :style="{'float':'right'}"
          :current-page=currentPage
          :page-sizes="[2, 10, 20, 30, 40]"
          :page-size=pageSize
          layout="total, sizes, prev, pager, next, jumper"
          :total=total>
        </el-pagination>
      </el-col>
    </el-row>

    <!--  -->
    <el-dialog :title="title" :visible.sync="open" width="600px" append-to-body>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="name">
              <el-input v-model="form.name" placeholder=""/>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="price">
              <el-input v-model="form.price" placeholder=""/>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="count">
              <el-input v-model="form.count" placeholder=""/>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="count">
              <el-input v-model="form.purchaser" placeholder=""/>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm"> </el-button>
        <el-button @click="cancel"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: 'Maintain',
    data () {
      return {
        // 
        loading: false,
        // 
        ids: [],
        // 
        showSearch: true,
        // 
        single: true,
        // 
        multiple: true,
        // 
        form: {},
        // 
        dateRange: [],
        // 
        open: false,
        // 
        title: '',
        // 
        total: 0,
        // 
        buildCostList: null,
        // 
        currentPage: 1,
        // 
        pageSize: 10,
        // 
        queryParams: {
          pageNum: 1,
          pageSize: 10,
          name: undefined,
          buyTime: undefined,

        },
        // 
        columns: [
          {key: 0, label: ``, visible: true},
          {key: 1, label: ``, visible: true},
          {key: 2, label: ``, visible: true},
          {key: 3, label: ``, visible: true},
          {key: 4, label: ``, visible: true},
          {key: 5, label: ``, visible: true}
        ],

        // 
        rules: {
          name: [
            {required: true, message: '', trigger: 'blur'}
          ],
          price: [
            {required: true, message: '', trigger: 'blur'}
          ],
          count: [
            {required: true, message: '', trigger: 'blur'}
          ],
          purchaser: [
            {
              message: '',
              trigger: ['blur', 'change']
            }
          ],
          buyTime: [
            {
              message: '',
              trigger: 'blur'
            }
          ]
        }
      }
    },
  }
</script>

<style scoped>

</style>



